<template lang="html">
  <div class="Add-article">
    <Row type="flex" justify="start" class="code-row-bg article-row">
      <Col span="4" class="text-right">{{$t('common.Affiliation')}}：</Col>
      <Col span="6" class="mgl-20"> <Input  placeholder="" style="width: 270px"></Input></Col>
      <Col span="2"><Checkbox >{{$t('common.External_link')}}</Checkbox></Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg article-row">
      <Col span="4" class="text-right">{{$t('common.Title')}}：</Col>
      <Col span="7" class="mgl-20"> <Input  placeholder="" style="width: 330px"></Input></Col>
      <Col span="4">{{$t('common.Colour')}}：
        <Select  style="width:80px" :placeholder="this.$t('common.Default')">
            <Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
        </Select>
      </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg article-row">
      <Col span="4" class="text-right">{{$t('common.Keyword')}}：</Col>
      <Col span="7" class="mgl-20"> <Input  placeholder="" style="width: 330px"></Input></Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg article-row">
      <Col span="4" class="text-right">{{$t('common.SID')}}：</Col>
      <Col span="7" class="mgl-20"> <Input  placeholder="" style="width: 330px"></Input></Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg article-row">
      <Col span="4" class="text-right">{{$t('common.Weight')}}：</Col>
      <Col span="3" class="mgl-20"> <Input  placeholder="0" style="width: 130px"></Input></Col>
      <Col span="2"><Checkbox >{{$t('common.Sticky')}}</Checkbox></Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg text-area">
      <Col span="4" class="text-right">{{$t('common.Summary')}}：</Col>
      <Col span="8" class="mgl-20"> <Input  type="textarea" :rows="4" placeholder="" style="width: 330px"></Input> </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg article-upload">
      <Col span="4" class="text-right">{{$t('common.Cover')}}：</Col>
      <Col span="2" class="mgl-20 z-99999">
        <Upload action="//jsonplaceholder.typicode.com/posts/">
            <Button icon="ios-cloud-upload-outline">{{$t('common.Selecte_files')}}</Button>
        </Upload>
      </Col>
      <Col span="4">
        <p class="col-gray mgl-30">{{$t('common.No_files_selected')}}</p>
      </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg article-row">
      <Col span="4" class="text-right">{{$t('common.Author')}}：</Col>
      <Col span="7" class="mgl-20"> <Input  placeholder="" style="width: 330px"></Input></Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg text-edit">
      <Col span="4" class="text-right">{{$t('common.Content')}}：</Col>
      <Col span="14" class="mgl-20">
        <div ref="editor" style="text-align:left"><p>{{ editorContent }}</p></div>
      </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg article-row">
      <Col span="4" class="text-right">{{$t('common.Source')}}：</Col>
      <Col span="7" class="mgl-20"> <Input  placeholder="" style="width: 330px"></Input></Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg article-row">
      <Col span="4" class="text-right">{{$t('common.Release_time')}}：</Col>
      <Col span="7" class="mgl-20" style="z-index:99999">
        <DatePicker type="date" :placeholder="this.$t('common.Select_date')" style="width: 200px"></DatePicker>
      </Col>
    </Row>
    <Row type="flex" justify="start" class="code-row-bg article-save">
      <Col span="2" >
        <Button type="primary" @click="succ">{{$t('common.Save')}}</Button>
      </Col>
      <Col span="1" class="mgl-20" >
        <Button @click="Return">{{$t('common.Return')}}</Button>
      </Col>
    </Row>
  </div>
</template>

<script>
import E from 'wangeditor'
export default {
  data (){
    return {
      cityList: [
            {
                value: 'red',
                label: this.$t('common.Red')
            },
            {
                value: 'blue',
                label: this.$t('common.Blue')
            },
            {
                value: 'orange',
                label: this.$t('common.Orange')
            }
        ],
        editorContent: '',
    }
  },
  methods:{
    succ () {
      this.$Message.success({
        content: this.$t('common.Save_success'),
        duration: 2
      });
      setTimeout(() =>{
        this.$router.push('/Wait_article');
      },2000)
    },
    getContent: function () {
      alert(this.editorContent)
    },
    Return () {
      this.$router.push('/Wait_article');
    }
  },
  mounted() {
    var editor = new E(this.$refs.editor)
    editor.customConfig.onchange = (html) => {
      //this.editorContent = html
    }
    editor.create()
  }
}
</script>

<style lang="css">
.Add-article{
  background: #fff;
  padding: 30px;
}
.Add-article .article-row{
  height: 45px;
  line-height: 45px;
  border-bottom: 1px dotted #ccc;
  text-align: left;
}
.Add-article .article-upload{
  border-bottom: 1px dotted #ccc;
  text-align: left;
  line-height: 45px;
}
.Add-article .text-area{
  height: 110px;
  border-bottom: 1px dotted #ccc;
  text-align: left;
  padding-top: 8px;
}
.Add-article .text-edit{
  padding-top: 8px;
  border-bottom: 1px dotted #ccc;
  text-align: left;
  padding-bottom: 10px;
}
.Add-article .article-save{
  margin: 20px 0 20px 300px;
}
</style>



